import React, { useEffect } from "react";
import { Table, Divider, Button, Col, Pagination, Spin } from "antd";
import { connect } from "react-redux";
import {
  getUserDeviceList,
  SetDeviceParams,
} from "../../../store/userProfile/profileSlice";
import moment from "moment";

function userDevices(props) {
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";

  useEffect(() => {
    props.getUserDeviceList({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
    },
    {
      title: "Code",
      width: 150,
      render: (row) => <span className={columnClass}>{row.code}</span>,
    },
    {
      title: "Device Name",
      width: 150,
      render: (row) => <span className={columnClass}>{row.name}</span>,
    },
    {
      title: "Device Type",
      width: 150,
      render: (row) => <span className={columnClass}>{row.type_name}</span>,
    },
    {
      title: "Status",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {row.is_active ? "active" : "in-active"}
        </span>
      ),
    },
    // {
    //   title: "Logout",
    //   width: 200,
    //   render: (row) => <span className={columnClass}>{moment(row.logout).format("DD-MM-YYYY, h:mm:ss a")}</span>,
    // },
  ];

  function showTotal(total) {
    return "Total " + total + " items";
  }

  function onPageChange(page, pageSize) {
    props.SetDeviceParams({
      ...props.UserDeviceParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getUserDeviceList({
      ...props.UserDeviceParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  const data = props.userDeviceList.map((row, key) => ({
    ...row,
    index:
      (props.UserDeviceParams.page - 1) * props.UserDeviceParams.page_size +
      key +
      1,
  }));

  return (
    <div className="hp-profile-activity">
      <h2 className="hp-mb-4"> Devices</h2>

      <Table
        className="hp-overflow-scroll hp-border-1 hp-border-color-dark-80 hp-border-bottom-none hp-border-radius"
        columns={columns}
        dataSource={data}
        pagination={false}
        loading={props.loading}
        size="middle"
      />

      <Pagination
        size="small"
        total={props.listCount}
        current={props.UserDeviceParams?.page}
        pageSize={props.UserDeviceParams?.page_size}
        showTotal={showTotal}
        showSizeChanger
        onChange={onPageChange}
        className="hp-mb-12 hp-mt-24 hp-float-right"
      />
    </div>
  );
}
function mapStateToProps({ userProfile }) {
  return {
    userDeviceList: userProfile.userDeviceList,
    apiError: userProfile.apiError,
    UserDeviceParams: userProfile.UserDeviceParams,
    noofpages: userProfile.noofpages,
    listCount: userProfile.listCount,
    loading: userProfile.loading,
  };
}

export default connect(mapStateToProps, {
  getUserDeviceList,
  SetDeviceParams,
})(userDevices);
