import React, { useState, useEffect } from "react";
import { Row, Col, Divider, Form, Input, Button, Alert } from "antd";
import {
  getUser,
  changePasswordModalShow,
  InputChangeValue,
  ChangePasswordSubmit,
} from "../../../store/userProfile/profileSlice";
import { connect } from "react-redux";

function PasswordProfile(props) {
  const [confimPasswordError, setconfimPasswordError] = useState(false);
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  useEffect(() => {
    props.getUser();
  }, []);

  const onSubmit = () => {
    if (props.passwordData.oldPassword !== "") {
      // this.setState({
      //   oldPasswordError: false,
      // });
      if (props.passwordData.newPassword !== "") {
        var data = {
          old_password: props.passwordData.oldPassword,
          password: props.passwordData.newPassword,
        };

        props.ChangePasswordSubmit(data);
      }
    } else {
      // this.setState({
      //   oldPasswordError: true,
      // });
    }
  };

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  function confirmPasswordHandeler(e) {
    if (e.target.value == props.passwordData.newPassword) {
      setconfimPasswordError(false);
      props.InputChangeValue({
        key: e.target.name,
        value: e.target.value,
      });
    } else {
      setconfimPasswordError(true);
    }
  }

  return (
    <Row>
      <Col span={24}>
        <h2>Change Password</h2>
        <p className="hp-p1-body hp-mb-0">
          Set a unique password to protect your account.
        </p>

        <Divider className={dividerClass} />
      </Col>

      <Col xxl={8} xl={8} md={12} xs={24} span={24}>
        <Form layout="vertical" name="basic" initialValues={{ remember: true }}>
          <Form.Item label="Old Password" name="oldPassword">
            <Input.Password
              id="oldPassword"
              name="oldPassword"
              style={{ width: "100%" }}
              placeholder="Old Password"
              value={props.passwordData?.oldPassword}
              onChange={ChangeHandeler}
              rules={[
                {
                  required: true,
                  message: "Please input your Old password!",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="New Password" name="newPassword">
            <Input.Password
              id="newPassword"
              name="newPassword"
              style={{ width: "100%" }}
              placeholder="Old Password"
              value={props.passwordData?.newPassword}
              onChange={ChangeHandeler}
              rules={[
                {
                  required: true,
                  message: "Please input your New password!",
                },
              ]}
            />
          </Form.Item>
          <Form.Item label="Confim Password" name="confimPassword">
            <Input.Password
              id="confimPassword"
              name="confimPassword"
              style={{ width: "100%" }}
              placeholder="Old Password"
              value={props.passwordData?.confimPassword}
              onChange={confirmPasswordHandeler}
              rules={[
                {
                  required: true,
                  message: "Please input your Confim password!",
                },
              ]}
            />
          </Form.Item>
          {confimPasswordError && (
            <Alert message="Passwords don't match" type="error" />
          )}

          {props.apiError?.old_password && (
            <Alert
              message={props.apiError?.old_password?.old_password}
              type="error"
            />
          )}

          <Col md={24} span={24} className="hp-mt-10">
            <Button block type="primary" htmlType="submit" onClick={onSubmit}>
              Submit
            </Button>
          </Col>
        </Form>
      </Col>
    </Row>
  );
}
function mapStateToProps({ userProfile }) {
  return {
    userData: userProfile.userData,
    changePasswordModel: userProfile.model,
    passwordData: userProfile.passwordData,
    apiError: userProfile.apiError,
  };
}

export default connect(mapStateToProps, {
  getUser,
  changePasswordModalShow,
  InputChangeValue,
  ChangePasswordSubmit,
})(PasswordProfile);
