import React, { useEffect } from "react";
import { Table, Divider, Button, Col, Pagination, Spin } from "antd";
import { connect } from "react-redux";
import {
  getActivityLog,
  SetActivityLogParams,
} from "../../../store/userProfile/profileSlice";
import moment from "moment";

function ActivityProfile(props) {
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";

  useEffect(() => {
    props.getActivityLog({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
    },
    {
      title: "Device Name",
      width: 130,
      render: (row) => <span className={columnClass}>{row.device?.name}</span>,
    },
    {
      title: "Device Type",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.device?.type_name}</span>
      ),
    },
    {
      title: "Login",
      width: 220,
      render: (row) => (
        <span className={columnClass}>
          {moment(row.login).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Logout",
      width: 220,
      render: (row) => (
        <span className={columnClass}>
          {moment(row.logout).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
  ];

  function showTotal(total) {
    return "Total " + total + " items";
  }

  function onPageChange(page, pageSize) {
    props.SetActivityLogParams({
      ...props.activityLogParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getActivityLog({
      ...props.activityLogParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  const data = props.activityLogList.map((row, key) => ({
    ...row,
    index:
      (props.activityLogParams.page - 1) * props.activityLogParams.page_size +
      key +
      1,
  }));

  return (
    <div className="hp-profile-activity">
      <h2 className="hp-mb-4">Login Activity</h2>
      <Spin tip="Loading..." spinning={props.loading} delay={500}>
        <Table
          className="hp-overflow-scroll hp-border-1 hp-border-color-dark-80 hp-border-bottom-none hp-border-radius"
          columns={columns}
          dataSource={data}
          pagination={false}
          size="middle"
        />
      </Spin>
      <Pagination
        size="small"
        current={props.activityLogParams?.page}
        pageSize={props.activityLogParams?.page_size}
        total={props.listCount}
        showTotal={showTotal}
        showSizeChanger
        onChange={onPageChange}
        className="hp-mb-12 hp-mt-24 hp-float-right"
      />
    </div>
  );
}
function mapStateToProps({ userProfile }) {
  return {
    activityLogList: userProfile.activityLogList,
    apiError: userProfile.apiError,
    activityLogParams: userProfile.activityLogParams,
    noofpages: userProfile.noofpages,
    listCount: userProfile.listCount,
    loading: userProfile.loading,
  };
}

export default connect(mapStateToProps, {
  getActivityLog,
  SetActivityLogParams,
})(ActivityProfile);
